body {
   margin: 0;
   padding: 0;
   font-family: sans-serif;
   background: linear-gradient(to left, #E5E8FB, #EDE3EB);
}



h1, h2, h3, h4, h5, h6, p {
   margin: 0;
   padding: 0;
}

.container {
   max-width: 1140px;
   width: 100%;
   padding: 0 15px;
   margin: 0 auto;

   box-sizing: border-box;
}