.userBlock {
   margin-top: 20px;
   position: relative;
   width: 300px;
   height: 100px;
   border: 2px solid black;
   border-radius: 10px;
   display: inline-flex;
   align-items: center;
   justify-content: space-between;
   padding: 0 10px;
   text-decoration: none;
   box-sizing: border-box;
   margin-right: 10px;
   background: #fff;

   @media only screen and(max-width:362px) {
      width: 100%;
      height: 60px;
      margin-right: 0;
      
   }

   &-info {
      margin-left: 10px;

      h1 {
         text-decoration: none;
         font-size: 20px;
         color: black;
         @media only screen and(max-width:362px) {
            font-size: 10px;
         }
      }

      span {
         text-decoration: none;
         @media only screen and(max-width:362px) {
            font-size: 10px;
         }
      }
   }

   &-header {
      display: flex;
      align-items: center;

      img {
         width: 50px;
         height: 50px;
         border-radius: 50%;
         border: 2px solid black;
         padding: 5px;
         @media only screen and(max-width:362px) {
            width: 25px;
            height: 25px;
         }
      }

      

      
   }
   .row {

      span {
         position: absolute;
         top: 10px;
         right: 10px;
      }
   }

}