.signUp {

   width: 100%;
   box-sizing: border-box;
   display: flex;
   align-items: center;
   justify-content: center;
   padding: 0 20px; 

   &-inner {
      width: 450px;
      background: white;
      box-sizing: border-box;
      padding: 25px;
      border-radius: 10px;
      margin-top: 50px;

      @media only screen and(max-width:576px) {
         width: 300px;
      }
   }

   &-header {
      margin-bottom: 40px;
      h1 {
         font-style: normal;
         font-weight: bold;
         font-size: 30px;
         line-height: 35px;
         color: #1A202C;

         @media only screen and(max-width:576px) {
            font-size: 20px;
         }
      }
   }


   .row {
      display: flex;
      flex-direction: column;
      margin-bottom: 10px;

      input {
         margin-top: 5px;
         padding: 10px;
         border: 1px solid #E8E8E8;
         box-sizing: border-box;
         border-radius: 5px;
         background: #F5F4F9;
         outline: none;

         &:focus {
            box-shadow: 0 0 5px #B5BED6;
         }
      }

      button, a {
         background: #014492;
         border-radius: 5px;
         padding: 10px;
         color: white;
         border: none;
         text-align: center;
         text-decoration: none;
      }
   }
}

