.user {
   position: relative;
   width: 100%;
   box-sizing: border-box;
   display: flex;
   flex-direction: column;
   padding: 0 20px;

   &-sidebar {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding: 5px 0;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      background: #fff;
      z-index: 100000;

      @media only screen and(max-width:576px) {
         justify-content: center;
         top: auto;
         bottom: 0;

         
         
         button {
            background: transparent;
            margin-right: 10px;
            border-radius: 50%;
            border: none;
            display: flex;
            align-items: center;
            justify-content: center;
            outline: none;
            &:focus {
               outline: none;
            }

            img {
               width: 15px;
               height: 15px;
               border-radius: 50%;
               padding: 5px;
            }

            &.active {
               img {
                  box-shadow: inset 0 0 10px black;
               }
            }
         }
      }

      button {
         background: transparent;
         margin-right: 10px;
         border-radius: 50%;
         border: none;
         display: flex;
         align-items: center;
         justify-content: center;

         img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            padding: 5px;
         }

         &.active{
            img {
               box-shadow:  inset 0 0 10px black;
            }
         }
      }
   }

   @media only screen and(max-width:576px) {
      flex-direction: column-reverse;
   }

   &-body {
      margin-top: 80px;
      @media only screen and(max-width:576px) {
         margin-top: 20px;
         margin-bottom: 80px;
      }
   }
   &-inner {
      width: 450px;
      background: white;
      box-sizing: border-box;
      padding: 25px;
      border-radius: 10px;

      @media only screen and(max-width:576px) {
         width: 300px;
      }
   }

   &-header {
      margin-bottom: 40px;

      h1 {
         font-style: normal;
         font-weight: bold;
         font-size: 30px;
         line-height: 35px;
         color: #1A202C;

         @media only screen and(max-width:576px) {
            font-size: 20px;
         }
      }
   }


   .row {
      display: flex;
      flex-direction: column;
      margin-bottom: 10px;

      label {
         @media only screen and(max-width:576px) {
            font-size: 12px;
         }
      }

      input {
         margin-top: 5px;
         padding: 10px;
         border: 1px solid #E8E8E8;
         box-sizing: border-box;
         border-radius: 5px;
         background: #F5F4F9;
         outline: none;

         &:focus {
            box-shadow: 0 0 5px #B5BED6;
         }
      }

      button,
      a {
         background: #014492;
         border-radius: 5px;
         padding: 10px;
         color: white;
         border: none;
         text-align: center;
         text-decoration: none;
      }
   }
}

.admin {
   
   &-header{
      border-bottom: 2px solid black;
      padding: 20px;
   }

   a {
      text-decoration: none;
      box-sizing: border-box;
      span {
         color: black;
      }

      &:hover{
         .userBlock {
            box-shadow: 0 0 5px rgb(1, 140, 255);
         }
      }
   }


}

.userInfo {
   &-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px ;
      border-bottom: 2px solid black;
      box-sizing: border-box;


      &-left {
         display: flex;
         align-items: center;

         img {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            border: 2px solid black;
            padding: 5px;

            @media only screen and(max-width:576px) {
               width: 25px;
               height: 25px;
            }
         }
         
      }
   }

   &-title {
      margin-left: 10px;

      h2 {
         @media only screen and(max-width:576px) {
            font-size: 12px;
            
         }
      }

      span{
         @media only screen and(max-width:576px) {
            font-size: 12px;
            
         }
      }
   }

   &-info {
      margin-top: 10px;

      .row {
         display: flex;
         align-items: center;
         padding: 10px 10px;
         border-bottom: 1px solid black;
         box-sizing: border-box;
         

         h3 {
            width: 50%;
             @media only screen and(max-width:576px) {
                font-size: 12px;
                width: 70%;
             }
         }

         span {
            margin-left: 20px;
            width: 50%;

             @media only screen and(max-width:576px) {
                width: 30%;
                font-size: 10px;
                display: flex;
                justify-content: flex-end;
             }
         }
      }
   }
}

.user-body {
   display: flex;
   align-items: center;
   justify-content: center;
}